import styled, { keyframes } from "styled-components";

const tonext = keyframes`
  75% {
    left: 0;
  }
  95% {
    left: 100%;
  }
  98% {
    left: 100%;
  }
  99% {
    left: 0;
  }
`;

const tostart = keyframes`
  75% {
    left: 0;
  }
  95% {
    left: -300%;
  }
  98% {
    left: -300%;
  }
  99% {
    left: 0;
  }
`;

const snap = keyframes`
  96% {
    scroll-snap-align: center;
  }
  97% {
    scroll-snap-align: none;
  }
  99% {
    scroll-snap-align: none;
  }
  100% {
    scroll-snap-align: center;
  }
`;

export const Wrapper = styled.div`
  position: relative;
  width: 100%;
`;
export const Container = styled.ul`
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 15px;

  overflow-x: scroll;
  scroll-behavior: smooth;
  scroll-snap-type: x mandatory;

  &::-webkit-scrollbar {
    display: none;
  }

  @media screen and (${({ theme }) => theme.media.min("tablet")}) {
    margin-bottom: 0;
  }

  &:hover > li::before,
  &:focus-within > li::before {
    animation-name: none;
  }
`;

export const SPage = styled.li`
  position: relative;

  flex: 0 0 100%;

  display: flex;
  flex-direction: column-reverse;

  &::before {
    content: "";

    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;

    scroll-snap-align: center;

    @media (hover: hover) {
      animation-name: ${tonext}, ${snap};

      animation-timing-function: ease;
      animation-duration: 10s;
      animation-iteration-count: infinite;
    }
  }

  @media screen and (${({ theme }) => theme.media.min("tablet")}) {
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    padding: 0
      ${({ theme }) => theme.ratio(20, 120, "tablet") + " 0 " + theme.ratio(10, 100, "tablet")};
  }

  &:last-child::before {
    @media (hover: hover) {
      animation-name: ${tostart}, ${snap};
    }
  }
`;

export const PageLeft = styled.section`
  @media screen and (${({ theme }) => theme.media.min("tablet")}) {
    flex: 0 0 330px;
    margin-bottom: ${({ theme }) => theme.ratio(20, 90, "tablet")};
  }

  h5 {
    font-weight: 700;
    font-size: ${({ theme }) => theme.ratio(20, 24, "mobileS")};
    line-height: 1.5;

    color: #1b5a7d;
    margin-bottom: 20px;
  }
`;
export const ButtonsContainer = styled.div`
  text-align: center;

  button:first-child {
    margin-right: 20px;
  }

  @media screen and (${({ theme }) => theme.media.min("tablet")}) {
    text-align: left;
  }
`;

export const PageRight = styled.div`
  flex: 0 0 40%;
  position: relative;
`;
export const ImageBlock = styled.div`
  position: relative;
  padding-bottom: 100%;

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  &::after {
    content: "Only ${({ price }) => price}$";

    position: absolute;
    right: 0;
    bottom: 10%;
    width: ${({ theme }) => theme.ratio(80, 40, "mobileS")};
    height: ${({ theme }) => theme.ratio(80, 40, "mobileS")};
    padding: ${({ theme }) => theme.ratio(15, 10, "mobileS")};

    font-weight: 600;
    font-size: ${({ theme }) => theme.ratio(16, 4, "mobileS")};
    line-height: 1.6;
    color: #ffffff;
    background: ${({ theme }) => theme.color.orange};
    text-align: center;

    border-radius: 50%;

    @media screen and (${({ theme }) => theme.media.min("tablet")}) {
      right: ${({ theme }) => theme.ratio(-10, -22, "tablet")};
    }
  }
`;

export const Controls = styled.div`
  display: flex;
  justify-content: center;
  gap: 8px;

  @media screen and (${({ theme }) => theme.media.min("tablet")}) {
    position: absolute;
    left: 15%;
    bottom: ${({ theme }) => theme.ratio(0, 55, "mobileS")};
    z-index: 1;
  }
`;

export const SControl = styled.button`
  height: 16px;
  width: 16px;

  border-radius: 50%;

  transition: background 0.1s linear;

  ${({ active, theme }) =>
    active ? `background: ${theme.color.orange}` : `border: 1px solid #adadad`}
`;
