import React, { memo, useCallback } from "react";
import { useNavigate } from "react-router-dom";

import { PageLeft, PageRight, SPage, ImageBlock, ButtonsContainer } from "./Slider.styled";
import Button from "../core-ui/button/Button";

const Page = ({ product, index }) => {
  const navigate = useNavigate();

  const ClickHandler = useCallback(() => navigate(`/products/${product.id}`), [product]);

  return (
    <SPage
      key={product.id}
      tabIndex={0}>
      <PageLeft>
        <h5>{product.title}</h5>
        <ButtonsContainer>
          <Button color={"orange"}>Shop now</Button>
          <Button
            outlined
            onClick={ClickHandler}
            color={"blue"}>
            View more
          </Button>
        </ButtonsContainer>
      </PageLeft>
      <PageRight>
        <ImageBlock price={product.price}>
          <img
            src={product.imageUrl || ""}
            alt={product.title}
            loading={index ? "lazy" : undefined}
          />
        </ImageBlock>
      </PageRight>
    </SPage>
  );
};

export default memo(Page);
